.hexagonal-grid {
    width: 475px;
    margin-left: 130px;
    position: relative;
    height: 520px;
}

.game-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh; /* Ensure the container takes at least the full viewport height */
    padding: 20px 0; /* Add some padding at the top and bottom */
}

.game-controls {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px; /* Spacing between controls and grid */
}

.game-instructions {
    margin-top: 20px; /* Spacing above the instructions */
    padding: 20px;
    border-radius: 5px; /* Optional border radius */
    width: 80%; /* Adjust width as needed */
    text-align: center; /* Center the instructions text */
}

.score {
    margin: 10px;
    font-size: 20px;
    font-weight: bold;
}

/* Update these styles in your GameBoard.css or the respective CSS file where button styles are defined */

button {
    padding: 10px 20px; /* Adjust padding to ensure buttons are comfortably clickable */
    font-size: 16px; /* Set a suitable font size for readability */
    color: #fff; /* Set text color to white for contrast */
    background-color: #007bff; /* Use a distinct background color; this is a blue shade */
    border: none; /* Remove the default border */
    border-radius: 5px; /* Add rounded corners */
    cursor: pointer; /* Change cursor to pointer to indicate clickability */
    transition: background-color 0.3s, transform 0.2s; /* Smooth transition for background color and transform */
    outline: none; /* Remove the outline to avoid the default focus highlight */
}

button:hover, button:focus {
    background-color: #0056b3; /* Darken the button color on hover for feedback */
    transform: scale(1.05); /* Slightly enlarge the button on hover */
}

/* Optionally, add specific styles for each button if they should look different */
.reset-button {
    background-color: #28a745;
}

.how-to-play-button {
    background-color: #17a2b8;
}

.reset-button:hover, .reset-button:focus {
    background-color: #218838;
}

.how-to-play-button:hover, .how-to-play-button:focus {
    background-color: #138496;
}
