.hexagon {
    position: absolute;
    width: 86.6px; /* Calculated width for a perfect hexagon with a height of 100px */
    height: 100px;
    background-color: #3498db;
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
    /* Clip path to create a hexagon shape with a point on top */
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: white;
    font-size: 24px;
    font-size: 1rem;
}

.hexagon.highlighted {
    background-color: yellow;
}

.invalid-hexagon {
    animation: pulseOpacity 2s infinite;
    background-color: rgba(255, 0, 0, 1)!important;
}

@keyframes pulseOpacity {
    0%, 100% {
        opacity: 0.5; 
    }
    50% {
        opacity: 0.8;
    }
}


