.tooltip {
    width: 120px;
    background-color: white;
    background-color: rgba(0, 0, 0, 0.5); /* Yellowish, semi-transparent */
    text-align: center;
    padding: 5px 5px;
    position: absolute;
    z-index: 1;
    color: white;
    transition: visibility 0s, opacity 0.3s ease-in 0s; /* Eases in */
}

.question-mark {
    position: absolute;
    top: 23px;
    right: 2px;
    cursor: pointer;
    display: inline-block;
    background-color: #ebff34;
    padding: 5px;
    border-radius: 40px;
    line-height: 6px;
    font-size: 13px;
    color: black;
    font-weight: bold;
    font-size: 14px;
}

.unselectable {
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none;    /* Firefox */
    -ms-user-select: none;     /* Internet Explorer/Edge */
    user-select: none;         /* Non-prefixed version, currently supported by Chrome, Opera, and Safari */
  }